import {
  ChangeDetectionStrategy,
  Component,
  ContentChild,
  ElementRef, EventEmitter, HostListener,
  Input, OnInit, Output,
  Renderer2,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { InputBoolean } from 'ng-zorro-antd/core';
import {CmacsCardTabComponent} from './cmacs-card-tab.component';

export type CmacsCardType = 'file' | 'selection' | 'action' | 'team' | 'project' | 'folder' | 'none';

@Component({
  selector: 'cmacs-card',
  exportAs: 'cmacsCard',
  preserveWhitespaces: false,
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  templateUrl: './cmacs-card.component.html',
  styles: [
    `
      cmacs-card {
        display: block;
      }
    `
  ],
  host: {
    '[class.ant-card-loading]': 'loading',
    '[class.ant-card-bordered]': 'bordered',
    '[class.ant-card-hoverable]': "hoverable || cmacsType === 'selection'",
    '[class.ant-card-type-inner]': `type === 'inner'`,
    '[class.ant-card-contain-tabs]': '!!tab',
    '[class.cmacs-card-files-wrapper]': "cmacsType === 'file'",
    '[class.cmacs-selection-card]': "cmacsType === 'selection'",
    '[class.cmacs-card-selected]': "cmacsType === 'selection' && selected",
    '[class.cmacs-card-disabled]': "cmacsType === 'selection' && disabled",
    '[class.cmacs-action-card]': "cmacsType === 'action'",
    '[class.cmacs-action-card-disabled]': "cmacsType === 'action' && disabled",
    '[class.cmacs-information-card]': "cmacsType === 'team'",
    '[class.cmacs-team-card]': "cmacsType === 'project'",
    '[class.cmacs-team-card-selected]': "cmacsType === 'project' && selected",
    '[class.cmacs-card-files-folders-wrapper]': "cmacsType === 'folder'",
    '[class.file-card-selected]': "cmacsType === 'folder' && selected"
  },
  styleUrls: ['./cmacs-card.component.css'],
})
export class CmacsCardComponent implements OnInit{
  folderIcon = 'folder';
  @Input() @InputBoolean() bordered = true;
  @Input() @InputBoolean() opened = false;
  @Input() @InputBoolean() editable = false;
  @Input() @InputBoolean() loading = false;
  @Input() @InputBoolean() selected = false;
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() hoverable = false;
  @Input() bodyStyle: { [key: string]: string };
  @Input() cover: TemplateRef<void>;
  @Input() actions: Array<TemplateRef<void>> = [];
  @Input() team: any = [];
  @Input() project: any = [];
  @Input() type: string;
  @Input() cmacsType: CmacsCardType = 'none';
  @Input() cmacsIcon: string = '';
  @Input() title: string | TemplateRef<void>;
  @Input() extra: string | TemplateRef<void>;
  @ContentChild(CmacsCardTabComponent) tab: CmacsCardTabComponent;
  @Output() open = new EventEmitter<any>();
  @Output() close = new EventEmitter<any>();

  constructor(renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'ant-card');
  }

  ngOnInit() {
    if (this.cmacsType === "folder") {
      this.folderIcon = this.opened ? 'folder-open' : 'folder';
    }
  }

  @HostListener('click', ['$event']) onClick(event: Event) {
    this.select(event);
  }

  @HostListener('dblclick') onDblClick() {
    if (this.cmacsType === 'folder') {
      this.folderIcon = this.folderIcon === 'folder' ? 'folder-open' : 'folder';
      this.opened = !this.opened;
      if (this.opened) {
        this.open.emit();
      } else {
        this.close.emit();
      }
    }
  }

  select(event: Event){
    event.preventDefault();
    event.stopPropagation();
    if (!this.disabled) {
      this.selected = !this.selected;
    }
  }

  getInitials(name: any) {
    let initials = name.match(/\b\w/g) || [];
    initials = ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
    return initials;
  }
}
